/* 기본 상태: 메인 영역만 스크롤 */
.mypage-wrapper {
    height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.mypage {
    display: flex;
    flex: 1;
    overflow: hidden;
    min-height: 100vh;
    background-color: #f9f9f9;
    padding-top: 70px;
}

/* 기본 메인 스크롤 영역 */
.content {
    flex: 1;
    overflow-y: auto;
    padding: 40px;
    max-width: 1500px;
    margin: 0 auto;
}

body.body-scroll .mypage-wrapper {
    height: auto;
    min-height: 100vh;
}

body.body-scroll .mypage {
    height: auto;
}

body.body-scroll .content {
    height: auto;
    overflow: visible;
}

.page-title {
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 30px;
}

.profile-header {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0,0,0,0.1);
    margin-bottom: 30px;
    min-height: 340px;
}

.avatar {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background-color: #ddd;
}

.info {
    flex: 1;
    margin-left: 40px;
}

.info h2 {
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 6px;
}

.email {
    font-size: 17px;
    color: #555;
    font-weight: 500;
    margin-bottom: 6px;
}

.bio {
    margin-top: 8px;
    color: #666;
}

.tags span {
    background-color: #e0e0ff;
    padding: 4px 8px;
    margin-right: 6px;
    border-radius: 8px;
    font-size: 16px;
}

.profile-edit-btn {
    background-color: #a684f1;
    color: white;
    border: none;
    padding: 12px 24px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 18px; /* 글자 크기 키우기 */
    text-decoration: none;
}

.stats {
    display: flex;
    gap: 20px;
    margin-bottom: 40px;
    width: 100%;
}

.stat-box {
    flex: 1 1 0;
    background-color: white;
    padding: 24px;
    border-radius: 12px;
    text-align: left;
    box-shadow: 0 2px 6px rgba(0,0,0,0.1);
    min-height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.stat-box h3 {
    font-size: 18px;
    color: #555;
    margin-bottom: 6px;
    margin-left: 12px;
}

.stat-box .count {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 8px;
    margin-left: 12px;
}

.stat-box .change {
    font-size: 15px;
    color: #888;
    margin-left: 12px;
}

.stat-box .change.up {
    color: green;
}

.stat-box .change.down {
    color: red;
}

.stat-box span {
    font-size: 2px;
    color: #888;
}

.recent-projects {
    margin-bottom: 60px;
    border-bottom: none; /* 선 제거 */
}

.recent-projects .section-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.view-all-btn {
    background-color: #f0f0f0;
    border-radius: 8px;
    padding: 8px 16px;
    text-decoration: none;
    font-size: 15px;
    color: #333;
    border: 1px solid #ccc;
    transition: all 0.2s ease-in-out;
}

.view-all-btn:hover {
    background-color: #e0e0e0;
    color: #000;
    border-color: #999;
}

.recent-projects .project-list {
    display: flex;
    gap: 20px;
}

.project-card {
    flex: 1 1 calc(33.333% - 20px);
    background-color: white;
    padding: 24px;
    border-radius: 16px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
    text-align: left;
    position: relative;
    min-height: 220px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-size: cover;
    background-position: center;
}

.thumbnail {
    width: 100%;
    height: 200px; /* 적당한 높이 설정 */
    object-fit: cover;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
    margin-bottom: 16px;
}


.project-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 6px 16px rgba(0,0,0,0.1);
}

.project-card h4 {
    font-size: 24px;
    font-weight: bold;
    color: #333;
    margin-bottom: 10px;
}

.project-card p {
    font-size: 16px;
    color: #666;
    line-height: 1.5;
    margin-bottom: 10px;
}

.project-card .tag {
    display: inline-block;
    font-size: 12px;           /* 작게 */
    background-color: #f1f1f1;
    color: #555;
    padding: 2px 4px;          /* 좌우 여백 줄이기 */
    border-radius: 4px;        /* 둥근 정도도 살짝 줄이기 */
    max-width: max-content;    /* 불필요한 너비 방지 */
}

.icon {
    margin-right: 40px;
    font-size: 40px;
    color: #888;
    vertical-align: middle;
    float: right; /* 아이콘을 오른쪽으로 배치 */
}

/* 🌙 전체 배경 및 텍스트 */
.dark-mode .mypage {
    background-color: #1e1e1e;
    color: #eee;
}

/* 🌙 컨텐츠 영역 */
.dark-mode .content {
    color: #eee;
}

/* 🌙 프로필 카드 */
.dark-mode .profile-header {
    background-color: #2a2a2a;
    box-shadow: 0 2px 6px rgba(255, 255, 255, 0.05);
}

/* 🌙 아바타 배경 */
.dark-mode .avatar {
    background-color: #444;
}

/* 🌙 이름, 아이디 */
.dark-mode .info h2 {
    color: #f5f5f5;
}

.dark-mode .bio {
    color: #bbb;
}

/* 🌙 태그 */
.dark-mode .tags span {
    background-color: #3a3a3a;
    color: #b88eff;
}

/* 🌙 프로필 편집 버튼 */
.dark-mode .edit-btn {
    background-color: #b88eff;
    color: #1e1e1e;
}

/* 🌙 통계 카드 */
.dark-mode .stat-box {
    background-color: #2a2a2a;
    box-shadow: 0 2px 6px rgba(255, 255, 255, 0.05);
}

.dark-mode .stat-box h3 {
    color: #ccc;
}

.dark-mode .stat-box .count {
    color: #eee;
}

.dark-mode .stat-box .change {
    color: #aaa;
}

/* 🌙 섹션 제목 */
.dark-mode .recent-projects .section-title {
    color: #eee;
}

/* 🌙 프로젝트 카드 */
.dark-mode .project-card {
    background-color: #2a2a2a;
    box-shadow: 0 4px 10px rgba(255, 255, 255, 0.05);
}

.dark-mode .project-card:hover {
    box-shadow: 0 6px 16px rgba(255, 255, 255, 0.08);
}

.dark-mode .project-card h4 {
    color: #f0f0f0;
}

.dark-mode .project-card p {
    color: #ccc;
}

.dark-mode .project-card .tag {
    background-color: #333;
    color: #aaa;
}

/* 🌙 전체 버튼 */
.dark-mode .view-all-btn {
    background-color: #2a2a2a;
    color: #b88eff;
    border: 1px solid #555;
}

.dark-mode .view-all-btn:hover {
    background-color: #3a3a3a;
    color: #fff;
    border-color: #888;
}

/* 🌙 아이콘 색상 */
.dark-mode .icon {
    color: #aaa;
}
